import React from 'react';
// @ts-ignore
import styles from './Button.module.css';
import propTypes from 'prop-types';
import Icon from './Icon';

const Button = props => {
  const icon = props.icon && (
    <div className={styles.buttonIcon}>
      <Icon name={props.icon} pulse={props.loading} />
    </div>
  );
  const contents = [
    <div className={styles.buttonText}>{props.children}</div>,
    icon,
  ];

  const buttonClicked = ev => {
    if (props.disabled || props.loading) return;
    props.onClick(ev);
  };

  return (
    <button
      className={`swee-component ${styles.button}`}
      disabled={props.disabled}
      type={props.type}
      data-color={props.color}
      data-size={props.size}
      data-loading={props.loading ? 'loading' : ''}
      data-selected={props.selected ? 'selected' : ''}
      onClick={buttonClicked}>
      {props.icon && props.iconRight ? contents : contents.reverse()}
    </button>
  );
};

Button.propTypes = {
  color: propTypes.string.isRequired,
  size: propTypes.string.isRequired,
  icon: propTypes.string,
  iconRight: propTypes.bool.isRequired,
  loading: propTypes.bool.isRequired,
  disabled: propTypes.bool.isRequired,
  selected: propTypes.bool.isRequired,
  children: propTypes.any,
  type: propTypes.string.isRequired,
  onClick: propTypes.func.isRequired,
};

Button.defaultProps = {
  color: 'default',
  size: 'normal',
  icon: null,
  iconRight: false,
  loading: false,
  disabled: false,
  selected: false,
  type: 'button',
  onClick: () => {},
};

export default Button;
