import React from 'react';
// @ts-ignore
import styles from './Icon.module.css';
import propTypes from 'prop-types';
import data from '../resources/icons/icons.json';

const Icon = props => {
  let icon = data.icons.find(icon => props.name === icon.name);
  if (!icon) {
    return null;
  }
  let svg = `<?xml version="1.0" encoding="UTF-8"?><svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="100px" height="100px" viewBox="0 0 100 100" version="1.1"><g id="surface1"><path d="${icon.source}"/></g></svg><!-- iconic normalized -->`;
  if (typeof svg !== 'string' || !svg.startsWith('<?xml')) {
    return null;
  }
  let size = {
    micro: '0.6em',
    small: '0.8em',
    normal: '1em',
    big: '1.2em',
    large: '1.4em',
  }[props.size];
  let rotate = `${props.rotate}deg`;
  if (props.spin) {
    svg = svg.replace('<svg', `<svg class="${styles.spin}"`);
  }
  return (
    <div
      className={`icon ${styles.iconContainer}`}
      style={{ display: 'inline-block' }}
      onClick={props.onClick}>
      <div
        className={`${styles.iconWrapper} ${props.pulse ? styles.pulse : ''}`}
        style={{ display: 'inline-block', fontSize: size }}>
        <div
          style={{
            display: 'inline-block',
            fill: props.color ? `var(--swee-color-${props.color})` : 'inherit',
            transform: `rotate(${rotate})`,
          }}
          dangerouslySetInnerHTML={{ __html: svg }}></div>
      </div>
    </div>
  );
};

Icon.data = data;

Icon.propTypes = {
  name: propTypes.string.isRequired,
  color: propTypes.string,
  size: propTypes.string.isRequired,
  rotate: propTypes.number.isRequired,
  pulse: propTypes.bool.isRequired,
  spin: propTypes.bool.isRequired,
  onClick: propTypes.func,
};

Icon.defaultProps = {
  color: null,
  size: 'normal',
  rotate: 0,
  pulse: false,
  spin: false,
  onClick: () => {},
};

export default Icon;
