import React, { useState } from 'react';
// @ts-ignore
import styles from './Accordion.module.css';
import propTypes from 'prop-types';
import Icon from './Icon';

const Accordion = props => {
  const sections = props.children;
  const [selected, setSelected] = useState(
    sections.filter(section => section.selected)
  );

  const sectionClicked = section => {
    if (selected.includes(section)) {
      setSelected(prevSelected => prevSelected.filter(sel => sel !== section));
      return;
    }
    if (props.multiple) {
      return setSelected(prevSelected => [...prevSelected, section]);
    }
    setSelected([section]);
  };

  return (
    <div className={`swee-component ${styles.accordion}`}>
      {sections.map(section => (
        <div className={styles.section} key={section.title}>
          <div
            className={styles.header}
            onClick={ev => sectionClicked(section)}>
            <div className={styles.title}>
              {section.icon && (
                <Icon
                  name={section.icon}
                  color={section.iconColor || 'default'}
                  size="small"
                />
              )}
              <span>{section.title}</span>
            </div>
            {section.desc && <div className={styles.desc}>{section.desc}</div>}
          </div>
          {selected.includes(section) && (
            <div className={styles.content}>{section.content}</div>
          )}
        </div>
      ))}
    </div>
  );
};

Accordion.propTypes = {
  children: propTypes.array,
  multiple: propTypes.bool.isRequired,
};

Accordion.defaultProps = {
  children: [],
  multiple: false,
};

export default Accordion;
